import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"

// Example: Accepts an employee prop
export function InfoCard({ employee, onEdit }: {
  employee: {
    id: string
    firstName: string
    lastName: string
    email?: string
    contactNumber: string
    role?: string
  },
  onEdit?: () => void
}) {
  return (
    <Card className="w-full max-w-sm">
      <CardHeader className="flex-row items-center justify-between">
        <CardTitle className="text-lg">
          {employee.firstName} {employee.lastName}
        </CardTitle>
        <Button size="sm" variant="outline" onClick={onEdit}>Edit</Button>
      </CardHeader>
      <CardContent>
        <div className="flex flex-col gap-2">
          <div className="text-sm text-muted-foreground">Role: <span className="font-medium text-foreground">{employee.role || "-"}</span></div>
          <div className="text-sm text-muted-foreground">Email: <span className="font-medium text-foreground">{employee.email || "-"}</span></div>
          <div className="text-sm text-muted-foreground">Contact: <span className="font-medium text-foreground">{employee.contactNumber}</span></div>
        </div>
      </CardContent>
      <CardFooter>
        <div className="text-xs text-muted-foreground">Employee ID: {employee.id}</div>
      </CardFooter>
    </Card>
  )
}
